import { baseRequest } from '../base'

//api for user management by the admin or manager (managing others)

const createUser = async (data, signal) => {
    try {
        return await baseRequest("api/v1/user/add", "POST", data, signal);
    } catch (e) {
        alert(e);
    }
};


