import React from 'react';
import { Row, Col, Card, Form, Button, InputGroup, FormControl, DropdownButton, Dropdown, FormFile } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import ErrorBody from '../../App/components/Reusables/ErrorBody';
import { getItemCatagories } from '../../api/inventory/itemCategory';
import Loader from '../../App/components/Reusables/Loader';

import Jhilikka from '../../App/components/Reusables/Jhilikka';
import { getRoles } from '../../api/userManagement/roles';
import { createUser } from '../../api/userManagement/users'
import { validateUserCreation } from '../../validation/user';
import DEMO from '../../globals/constant';


function getBase64(file, cb) {
    let reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function () {
        cb(reader.result)
    };
    reader.onerror = function (error) {
        console.log('Error: ', error);
    };
}

const Create = (props) => {

    const [loading, setLoading] = React.useState(true);
    const [submitting, setSubmitting] = React.useState(false)

    const [rolesArr, setRolesArr] = React.useState([]);

    const [userData, setUserData] = React.useState({
        username: '',
        email: '',
        fname: '',
        lname: '',
        password: '',
        password2: '',
        role: '',
    })

    const [userProfile, setUserProfile] = React.useState({
        address: '',
        phone1: '',
        phone2: '',
        post: '',
        profileImage: '',
        profileImageName: '',
    });

    const [errors, setErrors] = React.useState({});

    React.useEffect(() => {
        const controller = new AbortController();
        getRoles({ "action": "get", "filter": "none", "start": 0, "end": 999999 }, controller.signal)
            .then(async (res) => {
                console.log(res)
                if (res.json.status) {
                    await setRolesArr(res.json.roles);
                    await setLoading(false)
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });
        return () => controller.abort();
    }, []);

    const _userDataChange = (e) => {
        const { name, type, value } = e.target;
        setUserData({ ...userData, [name]: value });
        setErrors({ ...errors, [name]: '' })
    }

    const _userProfileChange = (e) => {
        console.log('here it changes')
        const { name, type, value } = e.target;
        setUserProfile({ ...userProfile, [name]: value });
        setErrors({ ...errors, [name]: '' });
    }
    const _profileImageChange = async (e) => {
        const image = e.target.files[0];
        console.log(image)
        getBase64(image, (result) => {
            // console.log(result);
            setUserProfile({ ...userProfile, profileImage: result, profileImageName: result, })
        })
    }
    const _removeProfileImage = () => {
        setUserProfile({ ...userProfile, profileImage: 'remove', profileImageName: null, })
    }

    console.log(userProfile)

    const _submit = () => {
        //check if there are errors

        const { isValid, errors } = validateUserCreation(userData);

        if (!isValid) {
            console.log(errors);
            setErrors({ ...errors })
            return;
        };

        setSubmitting(true)

        const requestJson = {
            "action": "add",
            "username": userData.username,
            "email": userData.email,
            "password": userData.password,
            "first_name": userData.fname,
            "last_name": userData.lname,
            "role_id": userData.role,
            "profile": {
                "address": userProfile.address,
                "phone_number": userProfile.phone1,
                "phone_number2": userProfile.phone2,
                "post": userProfile.post,
                "profile_image": userProfile.profileImage,
            }
        }
        //if no errros api call
        createUser(requestJson)
            .then(async (res) => {
                console.log(res);
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'User Created successfully!', background: '#dffff0' })
                    props.history.push('/users')
                } else {
                    setSubmitting(false)
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                setSubmitting(false)
            });
    }
    console.log(userProfile.address)

    if (loading) return <Loader />
    return (
        <Aux>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <h5>Create User</h5>
                            <hr />
                            <Row>

                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Username*</Form.Label>
                                        <Form.Control name="username" value={userData.username} onChange={_userDataChange} type="text" placeholder="Username" />
                                        <ErrorBody>{errors.username}</ErrorBody>
                                    </Form.Group>
                                </Col>


                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Email*</Form.Label>
                                        <Form.Control name="email" value={userData.email} onChange={_userDataChange} type="text" placeholder="Email" />
                                        <ErrorBody>{errors.email}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>First Name*</Form.Label>
                                        <Form.Control name="fname" value={userData.fname} onChange={_userDataChange} type="text" placeholder="First Name" />
                                        <ErrorBody>{errors.fname}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Last Name*</Form.Label>
                                        <Form.Control name="lname" value={userData.lname} onChange={_userDataChange} type="text" placeholder="Last Name" />
                                        <ErrorBody>{errors.lname}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Password*</Form.Label>
                                        <Form.Control name="password" value={userData.password} onChange={_userDataChange} type="password" placeholder="Password" />
                                        <ErrorBody>{errors.password}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Confirm Password*</Form.Label>
                                        <Form.Control name="password2" value={userData.password2} onChange={_userDataChange} type="password" placeholder="Confirm Password" />
                                        <ErrorBody>{errors.password2}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group >
                                        <Form.Label>Role*</Form.Label>
                                        <Form.Control name="role" value={userData.role} onChange={_userDataChange} as="select">

                                            <option value='' disabled >Select User Role</option>
                                            {rolesArr.map((instance, idex) => {
                                                return <option key={idex} value={instance.id}>{instance.name}</option>
                                            })}
                                        </Form.Control>
                                        <ErrorBody>{errors.role}</ErrorBody>
                                    </Form.Group>
                                </Col>
                            </Row>
                            <br />
                            <h5 style={{ fontSize: "1.2em" }}>Extra Details</h5>
                            <hr />
                            <Row>
                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Address</Form.Label>
                                        <Form.Control name="address" value={userProfile.address} onChange={_userProfileChange} type="text" placeholder="Address" />
                                        <ErrorBody>{errors.address}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Phone Np 1</Form.Label>
                                        <Form.Control name="phone1" value={userProfile.phone1} onChange={_userProfileChange} type="text" placeholder="Phone No 1" />
                                        <ErrorBody>{errors.phone1}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Phone No 2</Form.Label>
                                        <Form.Control name="phone2" value={userProfile.phone2} onChange={_userProfileChange} type="text" placeholder="Phone No 2" />
                                        <ErrorBody>{errors.phone2}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Post</Form.Label>
                                        <Form.Control name="post" value={userProfile.post} onChange={_userProfileChange} type="text" placeholder="Post" />
                                        <ErrorBody>{errors.post}</ErrorBody>
                                    </Form.Group>
                                </Col>


                                <Col md={4}>
                                    <Form.Group  >
                                        <Form.Label>Profile Image</Form.Label>
                                        <Form.File
                                            label={userProfile.profileImageName ? 'Change photo' : "No file selected"}
                                            data-browse="Browse"
                                            custom
                                            onChange={_profileImageChange}
                                        />
                                        <ErrorBody>{errors.profileImage}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                {userProfile.profileImageName ?
                                    <Col md={2}>
                                        <Form.Group  >
                                            <Form.Label>&#x200B;</Form.Label>
                                            <div >
                                                <img
                                                    src={userProfile.profileImageName}
                                                    alt=""
                                                    style={{ height: '42px', minWidth: '42px', width: '42px', objectFit: 'cover' }}
                                                />
                                                <i
                                                    onClick={_removeProfileImage}
                                                    className="feather icon-x text-c-red f-15 m-l-5 m-t-6"
                                                    style={{ cursor: 'pointer', border: '1px solid red', borderRadius: '3px' }}
                                                />
                                            </div>
                                        </Form.Group>
                                    </Col> :
                                    null
                                }


                            </Row>
                            <div className="d-flex justify-content-end" >
                                <Button
                                    variant="warning"
                                    onClick={() => { props.history.push('/users') }}
                                >
                                    Cancel
                                    </Button>
                                <Button
                                    disabled={submitting}
                                    variant="primary"
                                    onClick={_submit}
                                >
                                    {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Submitting</> : 'Submit'}
                                </Button>
                            </div>

                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Aux >
    );
}

export default Create;
