import { baseRequest } from '../base';
// api for items 



const getWeightUnit = async (data, signal) => {
  try {
    return await baseRequest('api/v1/inventory/items/categories/get', 'POST', data, signal);
  } catch (e) {
    alert(e)
  }
};




